p,
h1,
h2,
h3 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

img {
  display: block;
}

body {
  background-color:  #508effac;;
}
#form {
  background-color: #ffe8478e;
  padding-top: 20px;
  text-align: center;
  padding-bottom: 20px;
  border-bottom: 2px solid #84a278;
}
#input {
  color: #131313;
  outline: none;
  background-color: cornsilk;
  border: 1px solid #84a278;
  border-radius: 10px;
  padding: 11px 16px;
  font-size: 1.4rem;
  line-height: 1.4;
}
#categories-list {
  list-style: none;
  text-align: center;
}
#coctails-list {
  padding-bottom: 40px;
  padding-top: 40px;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 40px;
}
#coctails-list img{
    box-shadow: 0 5px 10px #777;
    transform: rotate(-3deg);  
}
#coctails-list h2{
    color: azure; 
    text-shadow: 2px 2px 4px #000000; 
    padding-top: 15px;
}
.li_markupCategoies {
  color: azure;
  font-weight: 700;
  font-size: 28px;
  line-height: 1.6;
  letter-spacing: 0.02em;
  text-shadow: 2px 2px 4px #000000;
}
.btn {
  background-color: #fff8dc;
  color: #131313;
  cursor: pointer;
  border: 2px solid #84a278;
  border-radius: 1rem;
  outline: none;
  padding: 10px 32px;
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.29;
}
.coctail_ingredient{
  list-style: none;
}
.markup_letter_item{
  color:#ffffff;
  margin-top: 10px;
}
.markup_letter_category{
  margin-bottom: 10px;
  margin-top: 10px;
}
.markup_letter_glass,
.markup_letter_category{
color: #ffffff;
}
.markup_by_ingredient{
    color: #ffffff;
    text-align: center;
    font-weight: 400;
font-size: 14px;
line-height: 1.86;
letter-spacing: 0.02em;
margin-top: 20px;
margin-bottom: 20px;
}
.markup_by_id{
    background: #8000666f;
    width: 50%;
    padding: 20px;
}
.markup_by_category,
.markup_by_glass{
color:#ffffff;
font-weight: 700;
font-size: 22px;
line-height: 1.16;
letter-spacing: 0.05em;
margin-top: 20px;
}
.markup_by_coctail_ingredient,
.markup_by_coctail_measure{
  list-style: none;
  color: #ffffff;
  font-weight: 400;
font-size: 18px;
line-height: 1.67;
letter-spacing: 0.02em;
}
.markup_by_id_div{
    display: flex;
    justify-content: space-around;
    gap: 20px;
    margin: 0 auto;
    width: 70%;
    background: linear-gradient(180deg, #171a244c 0%, rgba(23, 26, 36, 0.2) 100%);
}

